@import "styles/variables";

.button {
  background: $primary;
  color: $white;
  box-shadow: $shadow;
  padding: 0.625rem 1rem;
  border: none;
  font-weight: 700;
  font-size: 1rem;
  border-radius: 6px;
  cursor: pointer;
  min-width: 102px;
  &:disabled {
    background: $gray;
  }
}
